import humanizeDuration from "humanize-duration";

export function formatTime(date: Date | undefined): string {
  if (date) {
    const difference = new Date().getTime() - new Date(date).getTime();
    return humanizeDuration(difference, {
      largest: 1,
    });
  }

  return "-";
}
